import React, { useState } from 'react';
import { TonConnectUIProvider, useTonConnectUI, TonConnectButton } from '@tonconnect/ui-react';

const App = () => {
    const [tonAddress, setTonAddress] = useState(null);
    const { connected, connectWallet, disconnectWallet, wallet } = useTonConnectUI();

    const handleConnect = async () => {
        if (connected) {
            disconnectWallet();
        } else {
            const result = await connectWallet();
            if (result) {
                setTonAddress(result.address);
            }
        }
    };

    return (
        <TonConnectUIProvider manifestUrl="https://liomlong.github.io/pigminiapp/manifest.json">
            <div className="container">
                <h1>Welcome to Pig.tg</h1>
                <p>Pig.tg is your gateway to the decentralized world of TON blockchain. We offer premium .TG domains that are securely recorded as NFTs, ensuring your ownership and rights.</p>
                <TonConnectButton />
                {tonAddress && <p>Your TON Address: {tonAddress}</p>}
            </div>
        </TonConnectUIProvider>
    );
};

export default App;
